.splash-container {
    width: 100%;
    height: 40vh;
}

.splash-text-container {
    position: relative;
    display: table;
    top: 50%;
    left: 50%;
    width: fit-content;
    transform: translate(-50%, -50%);
    padding: 1.5vh;
}

.splash-info {
    display: table-cell;
    width: max-content;
    padding-right: 1vh;
}

.splash-headshot {
    display: table-cell;
    position: relative;
    width: 37%;
}

.splash-headshot img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 100%;
    border-radius: 20%;
}

.splash-info h1 {
    margin: 1vh;
    margin-top: 0;
    font-size: 3vh;
    color:#fff;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
    text-align: right;
}

.splash-info h2 {
    margin: .5vh 1vh;
    font-size: 2.5vh;
    color:#fff;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    font-style: italic;
    text-align: right;
}

.link-bar {
    display: flex;
    justify-content: space-around;
    margin: 1.5vh auto 0 auto;
    width: 100%;
    
}

.link-bar i {
    color: white;
    font-size: 2.5vh;
}
