.section-header {
    margin: 1.5vh;
    margin-top: 3vh;
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-size: 2.5vh;
    width: max-content;
}

.header-underline {
    height: 4px;
    animation:  background-color-cycle 10s linear 0s infinite normal none,
                shadow-color-cycle 10s linear 0s infinite normal none;
}