:root {
    --card-spacing: 1vh;
    --sm: 2;
    --md: 3;
    --lg: 4;
}

::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    border-radius: 10px;
}

::-webkit-scrollbar-thumb{
    border-radius: 10px;
    background: #444;
}

::-webkit-scrollbar-thumb:hover{
    background: #666; 
}

::-webkit-scrollbar-thumb:active{
    background: #888;
}

body {
    margin: 0;
    background-color: #121212;
}

.text-header {
    margin-bottom: 0;
    font-family: 'Montserrat', sans-serif;
    color: white;
}

.text-body {
    color:white;
    font-family: 'Roboto', sans-serif;
}

.panel {
    background-color: #080808cf;
    backdrop-filter: blur(0.4vh);
    border-radius: 1vh;
    box-shadow: 0.5vh 0.5vh 1.5vh #010101aa;
}

.text-container {
    margin: 0 auto;
    /* width: 60%; */
    padding: 1.5vh;
}

.text-container p {
    margin: 0;
}

.no-link {
    cursor: default;
    text-decoration: none !important;
}

a, .no-link {
    text-decoration: none;
    animation: text-color-cycle 10s linear 0s infinite normal none;
}

a:hover {
    text-decoration: underline;
}

#bg-anim {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    z-index: -1;
}

@keyframes background-color-cycle {
    0%   {background-color: #fcc;}
    16%  {background-color: #ffc;}
    33%  {background-color: #cfc;}
    50%  {background-color: #cff;}
    66%  {background-color: #ccf;}
    87%  {background-color: #fcf;}
    100% {background-color: #fcc;}
}

@keyframes shadow-color-cycle {
    0%   {box-shadow: 0 0 1vh #f00a;}
    16%  {box-shadow: 0 0 1.5vh #ff0a;}
    33%  {box-shadow: 0 0 1vh #0f0a;}
    50%  {box-shadow: 0 0 1.5vh #0ffa;}
    66%  {box-shadow: 0 0 1vh #00fa;}
    87%  {box-shadow: 0 0 1.5vh #f0fa;}
    100% {box-shadow: 0 0 1vh #f00a;}
}

@keyframes text-color-cycle {
    0%   {color: #faa;}
    16%  {color: #ffa;}
    33%  {color: #afa;}
    50%  {color: #aff;}
    66%  {color: #aaf;}
    87%  {color: #faf;}
    100% {color: #faa;}
}

/* mobile */
@media all and (max-width: 777.99px) {
    .container {
        width: 100%;
    }

    .content-container {
        margin: 0 auto;
        width: min-content;
    }

    .card-deck {
        --deckwidth: 250px;
        width: 250px;
    }

    .card {
        margin-top: 30px;
    }

    .card-header {
        opacity: 1 !important;
        font-size: 20px !important;
    }

    .section-header {
        font-size: 30px;
    }

    .scale-text-body {
        font-size: 14px;
    }

    .splash-container {
        height: 100vh;
    }

    .splash-text-container {
        top: 40%;
    }

    .splash-text-container h1 {
        font-size: 6vw;
    }

    .splash-text-container h2 {
        font-size: 3.5vw;
    }

    .splash-text-container .link-bar {
        font-size: 3vw;
    }
}

/* larger screens */
@media all and (min-width: 778px) {

    .container {
        width: 80%;
    }

    .card-deck {
        --deckwidth: 60vh;
        width: 100%;
    }

    .content-container {
        margin: 0 auto;
        width: 60vh;
    }

    .scale-text-body {
        font-size: 1.75vh;
    }
}