.sm {
    --size: var(--sm);
}

.md {
    --size: var(--md);
}

.lg {
    --size: var(--lg);
}

.card-deck.gallery {
    position: relative;
}

.card-deck.gallery .card:hover {
    z-index: 1;
}

.card-deck.gallery .card {
    position: absolute;
    transition-property: transform;
    transition-duration: 200ms;
    transition-timing-function: ease-out;
    perspective-origin: 50% 50%;
    perspective: 600px;
    transform-origin: top left;
    transform: translate(var(--left), var(--top));
    width: var(--width);
}

.card-deck.gallery .card.focus {
    z-index: 3;
    transform: translate(var(--centerX), var(--centerY)) scale(calc(2 / 3 / (.9 / 6 * var(--size) + .1 / 5 * (var(--size) - 1))));
}

.card-body {
    transition-property: transform, opacity, padding-bottom;
    transition-duration: 200ms;
    transition-timing-function: ease-out;
    transform-style: preserve-3d;

    position: relative;
    aspect-ratio: 1;
    border-radius: 0.5vh;
    background-size: 100%, 100%;
    overflow: hidden;
    user-select: none;
    box-shadow: 0.5vh 0.5vh 1vh #010101aa;
    z-index: 2;
}

.card-deck.gallery .card-body {
    cursor: pointer;
}

.card-deck.gallery .card.hidden .card-body {
    cursor: default;
    opacity: 0;
    transform: translateZ(-100px);
}

.card-deck.gallery .card.focus .card-body {    
    transition-duration: 0ms;
    transform: rotateX(var(--xRot)) rotateY(var(--yRot));
}

.card-deck.gallery .gallery.card .card-body:hover  {
    transform: translateZ(calc(10vh / var(--size))) rotateX(calc(15deg / var(--size)));
}

.card-overlay {
    width: 100%;
    aspect-ratio: 1;
    mix-blend-mode: lighten;
    transition: background-position 200ms ease-out;
    background-image: linear-gradient(10deg, #ffffff00 0%, #ffffff0a 46%, #ffffff1a 65%, #ffffff2c 72%, #ffffff11 100%);
    background-size: 200% 200%;
    background-position: 50% 70%;
}

.card-deck.gallery .card.focus .card-overlay {    
    transition-duration: 0ms;
    background-position: var(--xShift) var(--yShift);
}

.card-deck.gallery .gallery.card:hover .card-overlay {
    background-position: 50% calc(70% - 100% / var(--size));
}

.focus.card .card-overlay {
    transition: none;
}

.card-header {
    transition: opacity 200ms ease-out;
    position: absolute;
    padding: .4em .6em;
    margin: 0;
    bottom: 0; 
    left: 0;
    border-radius: 0 5px 0 0;
    background-color: rgba(0, 0, 0, .6);
}

.card .card-header {
    opacity: 0;
}

.gallery.card:hover .card-header {
    opacity: 1;
}

.focus.card .card-header {
    opacity: 1;
}

.card .card-header {
    font-size: calc(.014 * var(--deckwidth) * var(--size));
}

.card-link {
    text-align: center;
    width: 100%;
    letter-spacing: .2em;
}

.card-desc {
    position: absolute;
    padding: 1em;
    z-index: 0;
    transform: translate(0, -100%);
    max-height: 90%;
    width: 100%;
    box-sizing: border-box;
    overflow: hidden;
    transition: transform 0ms ease-out;
    transition-delay: 0ms;
    visibility: hidden;
}

.card-deck.gallery .card-desc {
    font-size: calc(2.5vh / 6 * var(--size));
}

.card-deck.mobile .card .card-body {
    transition: transform 200ms ease-out;
}

.card-deck.mobile .card.focus .card-body {
    transform: scale(1.07);
}

.card-deck.mobile .card {
    transition: padding 400ms ease-out;
    position: static;
    width: 250px;
}

.card-deck.mobile .card-desc {
    background-color: #080808dd;
    backdrop-filter: blur(4px);
    box-shadow: 1px 1px 5px #010101aa;
    border-radius: 5px;
    transition: transform 400ms ease-out,
                visibility 400ms;
    width: 250px;
}

.card-deck.mobile .card.focus {
    padding-bottom: 100%;
}

.card-desc p:first-child {
    margin-top: 0 !important;
    font-weight: 100;
}

.card-desc p:last-child {
    margin-bottom: 0 !important;
}

.focus.card .card-desc {
    visibility: visible;
    transform: translate(0, 0);
    transition-duration: 400ms;
}

.card-deck.gallery .focus.card .card-desc {
    transition-delay: 300ms;
}